<template>
  <v-container fluid>
    <v-layout row wrap align-center justify-center>
      <v-flex xs10>
        <v-expansion-panel expand>
          <v-expansion-panel-content value="1">
            <div slot="header" class="title purple--text">未读通知</div>
              <v-card>
                <v-container fluid style="min-height: 0;" grid-list-lg>
                  <v-layout row wrap>
                    <v-flex xs12 v-for="(notice,index) in unReadNotices" :key="index">
                      <v-card color="purple darken-2" class="white--text">
                        <v-card-title primary-title>
                          <v-flex xs12><div class="headline">{{ notice.title }}</div></v-flex>
                          <v-flex xs12 class="pt-0"><div>{{ notice.date }}</div></v-flex>
                        </v-card-title>
                        <v-card-text class="pt-0">
                          <v-flex xs12 class="pt-0"><div>{{ notice.content }}</div></v-flex>
                        </v-card-text>
                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn flat dark @click="readNotice(notice)">已读</v-btn>
                         </v-card-actions>
                      </v-card>
                     </v-flex>
                  </v-layout>
              </v-container>
            </v-card>
          </v-expansion-panel-content>
          <v-expansion-panel-content>
            <div slot="header" class="title purple--text">已读通知</div>
              <v-card>
              <v-container fluid style="min-height: 0;" grid-list-lg>
                <v-layout row wrap>
                  <v-flex xs12 v-for="(notice,index) in readNotices" :key="index">
                    <v-card color="grey lighten-3" class="purple--text" >
                      <v-card-title primary-title>
                        <v-flex xs12><div class="headline">{{ notice.title }}</div></v-flex>
                        <v-flex xs12 class="pt-0"><div>{{ notice.date }}</div></v-flex>
                      </v-card-title>
                      <v-card-text class="pt-0">
                        <v-flex xs12 class="pt-0"><div>{{ notice.content }}</div></v-flex>
                      </v-card-text>
                   </v-card>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
    export default {
      name: "SysNotice",
      data() {
        return {
          unReadNotices:[],
          readNotices:[]
        }
      },
      methods:{
        remove (item) {
          this.unReadNotices.splice(this.unReadNotices.indexOf(item), 1);
          this.unReadNotices = [...this.unReadNotices];
        },

        add(item) {
            this.readNotices.push(item);
        },

        readNotice(notice) {
          this.remove(notice);
          this.add(notice);
          this.$http.post('/notice/'+notice.id+'/read')
            .then((response) => {
              this.$emit('read-note');
            });
        },
      },
      created() {
        this.$http.get('/notice')
          .then((response) => {
            let notices = response.data;
            for(let i=0; i < notices.length; i++){
              if(notices[i].state === 0){
                this.unReadNotices.push(notices[i]);
                // this.unReadNotices=[...this.unReadNotices];
              }
              else this.readNotices.push(notices[i]);
            }
          })
      }
    }
</script>

<style scoped>

</style>



