<template>
  <v-card>
    <v-card-title>
      <span class="headline">用户数据统计</span>
    </v-card-title>
    <v-card-text>
      <v-container>
        <v-layout row>
          <v-flex xs4>
            <PieChart chart-id="sexChart" :data="sexChartData.data" :options="sexChartData.options"></PieChart>
          </v-flex>
          <v-flex xs4>
            <PieChart chart-id="ageChart" :data="ageChartData.data" :options="ageChartData.options"></PieChart>
          </v-flex>
          <v-flex xs4>
            <PieChart chart-id="userChart" :data="userChartData.data" :options="userChartData.options"></PieChart>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs12>
            <LineChart :data="lineChartData.data" :options="lineChartData.options" :height="lineChartData.height"></LineChart>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card-text>
  </v-card>
</template>

<script>
  import PieChart from '@/components/charts/PieChart';
  import LineChart from '@/components/charts/LineChart';
  export default {
    name: "UserStatics",
    components:{
      PieChart,
      LineChart
    },
    data() {
      return {
        sexChartData: {
          data: {
            datasets:[{
              data: [34, 20],
              backgroundColor: [
                'rgba(54, 162, 235, 0.8)',
                'rgba(255, 99, 132, 0.8)',
              ],
            }],
            labels: ['男性', '女性']
          },
          options: {
            responsive: true,
            title: {
              display: true,
              text: '工人男女比例',
              position: 'bottom'
            }
          },
        },
        ageChartData: {
          data: {
            datasets: [{
              data: [0,0,0,0],
              backgroundColor: [
                'rgba(54, 204, 255, 0.8)',
                'rgba(51, 102, 255, 0.8)',
                'rgba(51, 255, 204, 0.8)',
                'rgba(102, 255, 55, 0.8)',
              ],
            }],
            labels: ['18岁以下', '18-30', '31-45', '45岁以上']
          },
          options: {
            responsive: true,
            title: {
              display: true,
              text: '工人年龄分布',
              position: 'bottom',
            }
          },
        },
        lineChartData: {
          data: {
            datasets: [
              {
                label: '活跃工人数量',
                data: [],
                backgroundColor: ['rgba(250, 51, 0, 0.5)']
              },
              {
                label: '工人总数',
                data: [],
                backgroundColor: ['rgba(255, 100, 61, 0.5)']
              }],
            labels: this.getDateList(),
          },
          options: {
            responsive: true,
            title: {
              display: true,
              text: '活跃工人变化',
              position: 'bottom',
            }
          },
          height: 200
        },
        userChartData: {
          data: {
            datasets:[{
              data: [0, 0],
              backgroundColor: [
                'rgba(5, 152, 5, 0.8)',
                'rgba(0, 0, 132, 0.8)',
              ],
            }],
            labels: ['发布者', '工人']
          },
          options: {
            responsive: true,
            title: {
              display: true,
              text: '发布者-工人比例',
              position: 'bottom'
            }
          },
        }
      }
    },
    methods: {
      getDateList() {
        let dateList = [];
        let date = new Date();
        for (let i = 0; i < 7; i++) {
          date.setDate(date.getDate()-1);
          dateList.unshift((date.getMonth() + 1) + "-" + date.getDate());
        }
        return dateList;
      }
    },
    created() {
      console.log(11111);
      this.$http.get('/statistics/worker')
        .then((response) => {
          let data = response.data;
          this.sexChartData.data.datasets[0].data = [data.male, data.female];
          this.ageChartData.data.datasets[0].data = data.ageArray;
          this.lineChartData.data.datasets[0].data = data.activeArray;
          this.lineChartData.data.datasets[1].data = data.workerArray;
          this.userChartData.data.datasets[0].data[0] = data.male+data.female;
          let ctx = document.getElementById("line-chart");
          new Chart(ctx, {type: 'line', data: this.lineChartData.data, options: this.lineChartData.options});
          let ctxB = document.getElementById("sexChart");
          new Chart(ctxB, {type: 'pie', data: this.sexChartData.data, options: this.sexChartData.options});
          let ctxC = document.getElementById("ageChart");
          new Chart(ctxC, {type: 'pie', data: this.ageChartData.data, options: this.ageChartData.options});
          this.$http.get('/statistics/requester')
            .then((response) => {
              this.userChartData.data.datasets[0].data[1] = response.data.num;
              let ctx = document.getElementById("userChart");
              console.log(ctx);
              new Chart(ctx, {type: 'pie', data: this.userChartData.data, options: this.userChartData.options});
            })
      });
    },
  }
</script>

<style scoped>

</style>



