import VueRouter from 'vue-router'
import Index from './components/Index'
import Register from './components/Register'
import AdminPage from './components/admin/AdminPage'
import AdminInfo from './components/admin/AdminInfo'
import RequesterCheck from './components/admin/RequesterCheck'
import NoticePage from './components/admin/NoticePage'
import UserStatistic from './components/admin/UserStatistics'
import TaskStatistic from './components/admin/TaskStatistics'
import WorkerPage from './components/worker/WorkerPage'
import WorkerIndex from './components/worker/WorkerIndex'
import WorkerTask from './components/worker/WorkerTask'
import WorkerMark from './components/worker/WorkerMark'
import WorkerRank from './components/worker/WorkerRank'
import WorkerCenter from './components/worker/WorkerCenter'
import IndexPage from './components/admin/IndexPage'
import WorkerLogin from './components/WorkerLogin'
import RequesterLogin from './components/RequesterLogin'
import AdminLogin from './components/AdminLogin'
import RequesterIndex from './components/requester/RequesterIndex'
import TaskOverview from './components/requester/TaskOverview'
import IdleTask from './components/requester/IdleTask'
import UnderwayTask from './components/requester/UnderwayTask'
import FinishedTask from  './components/requester/FinishedTask'
import PersonalInfo from './components/requester/PersonalInfo'
import UpLoadFile from './components/requester/UpLoadFile'
import SysNotice from './components/requester/SysNotice'
import Garbage from './components/requester/Garbage'
import Draft from './components/requester/Draft'
const router = new VueRouter({
  routes: [
    {
      path: '/',
      name: 'Index',
      meta: {title: '首页 | AlphaCat众包数据标注系统'},
      component: Index
    },
    {
      path: '/login/worker',
      name: 'WorkerLogin',
      meta: {title: '工人登录 | AlphaCat众包数据标注系统'},
      component: WorkerLogin
    },
    {
      path: '/login/requester',
      name: 'PublisherLogin',
      meta: {title: '发布者登录 | AlphaCat众包数据标注系统'},
      component: RequesterLogin,
    },
    {
      path: '/login/admin',
      name: 'AdminLogin',
      meta: {title: '管理员登录 | AlphaCat后台管理系统'},
      component: AdminLogin
    },
    {
      path: '/register',
      name: 'Register',
      meta: {title: '注册 | AlphaCat众包数据标注系统'},
      component: Register
    },
    {
      path: '/admin',
      name: 'AdminPage',
      component: AdminPage,
      children: [
        {
          path: 'index',
          component: IndexPage,
          meta: {title: '首页 | 后台管理系统'},
        },
        {
          path: 'info',
          component: AdminInfo,
          meta: {title: '个人信息 | 后台管理系统'},
        },
        {
          path: 'check',
          component: RequesterCheck,
          meta: {title: '发布者审核 | 后台管理系统'},
        },
        {
          path: 'notice',
          component: NoticePage,
          meta: {title: '通知管理 | 后台管理系统'},
        },
        {
          path: 'userData',
          component: UserStatistic,
          meta: {title: '用户数据 | 后台管理系统'},
        },
        {
          path: 'taskData',
          component: TaskStatistic,
          meta: {title: '任务数据 | 后台管理系统'},
        },
      ],
    },
    {
      path: '/worker',
      name: 'WorkerPage',
      component: WorkerPage,
      children: [
        {
          path: 'index',
          component: WorkerIndex,
          meta: {title: '首页 | AlphaCat众包平台'},
        },
        {
          path: 'task',
          component: WorkerTask,
          meta: {title: '任务 | AlphaCat众包平台'},
        },
        {
          path: 'mark/:id',
          component: WorkerMark,
          meta: {title: '标注 | AlphaCat众包平台'},
        },
        {
          path: 'rank',
          component: WorkerRank,
          meta: {title: '排名 | AlphaCat众包平台'},
        },
        {
          path: 'center',
          component: WorkerCenter,
          meta: {title: '个人中心 | AlphaCat众包平台'},
        }
      ]
    },
    {
      path: '/requester',
      name: 'RequesterIndex',
      component: RequesterIndex,
      children: [
        {
          path: 'personalInfo',
          name: 'PersonalInfo',
          component: PersonalInfo
        },
        {
          path: 'index',
          name: 'TaskOverview',
          component: TaskOverview
        },
        {
          path: 'notice',
          name: 'Notice',
          component: SysNotice
        },
        {
          path: 'garbage',
          name: 'Garbage',
          component: Garbage
        },
        {
          path: 'draft',
          name: 'Draft',
          component: Draft
        },
      ]
    },
    {
      path: '/requester/idleTask/:taskId',
      name: 'IdleTask',
      component:IdleTask
    },
    {
      path: '/requester/underwayTask/:taskId',
      name: 'UnderwayTask',
      component: UnderwayTask
    },
    {
      path: '/requester/finishedTask/:taskId',
      name: 'FinishedTask',
      component: FinishedTask
    },
    {
      path: '/upload',
      name: 'Upload',
      component: UpLoadFile,
    }
  ]
});
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
});

export default router



