<template>
  <v-app id="idleTask">
    <v-toolbar color="purple darken-2" dark fixed app clipped-right id="toolbar">
      <v-toolbar-title>{{barTitle}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-tooltip bottom>
        <v-btn slot="activator" icon large target="_blank" to="/requester/index"><v-icon large>home</v-icon></v-btn>
        <span>返回主页</span>
      </v-tooltip>
    </v-toolbar>
    <v-content fluid fill-height >
      <v-card width="100%">
        <v-layout row align-center justify-center>
          <v-flex xs8>
            <v-card flat>
              <v-card-text>
                <v-container fluid>
                  <v-layout row>
                    <v-flex xs4><v-subheader>任务名称</v-subheader></v-flex>
                    <v-flex xs8><v-text-field :rules="[(v) => v.length <= 15 || 'Max 15 characters',(v)=>v.length>0 || 'Task name is needed']" :counter="15" name="taskName" label="Task Name" v-model="taskInfo.name" :disabled="edit" color="purple"></v-text-field></v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs4><v-subheader>任务类型</v-subheader></v-flex>
                    <v-flex xs8><v-text-field name="taskType" label="Task Type" :value="taskType" disabled></v-text-field></v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs4><v-subheader>任务描述</v-subheader></v-flex>
                    <v-flex xs8><v-text-field :rules="[(v) => v.length <= 150 || 'Max 150 characters']" :counter="150" name="taskDescription" label="Task Description" v-model="taskInfo.description" :disabled="edit" color="purple"></v-text-field></v-flex>
                  </v-layout>
                  <v-divider v-if="taskInfo.method===1" class="my-3"></v-divider>
                  <v-layout v-if="taskInfo.method===1" row>
                    <v-checkbox label="是否整体描述" v-model="taskInfo.hasWholeLabel" color="purple" :disabled="edit" class="pt-2"></v-checkbox>
                  </v-layout>
                  <v-divider></v-divider>
                  <v-subheader v-if="taskInfo.method===1" class="pl-1 pt-4">任务标签及预设选项</v-subheader>
                  <v-container v-if="taskInfo.method===1" fluid grid-list-sm class="pt-3">
                    <v-layout v-for="(tag,index) in taskInfo.labels" :key="tag.label" row wrap>
                      <v-select v-model="tag.choices" :label="tag.label" chips tags solo prepend-icon="bookmark_border"
                                append-icon=" " clearable color="purple" style="background: transparent" pd-1 :disabled="edit">
                        <template slot="selection" slot-scope="data">
                          <v-chip :selected="data.selected" close @input="remove(data.item,index)">
                            <strong>{{ data.item }}</strong>&nbsp;
                            <span>({{tag.label}})</span>
                          </v-chip>
                        </template>
                      </v-select>
                      <v-btn outline small fab color="purple" :disabled="edit" @click="removeLabel(index)" >
                        <v-icon class="pt-2">delete_forever</v-icon>
                      </v-btn>
                    </v-layout>
                  </v-container>
                  <v-layout v-if="taskInfo.method===1" row class="pr-5 pl-3">
                    <v-text-field :rules="[(v) => v.length <= 10 || 'Max 10 characters']" :counter="10" prepend-icon="bookmark"
                                  id="newLabelField" name="newLabelField" label="新标签名" color="purple" v-model="newLabelName"
                                  ref="newLabelField" :disabled="edit"></v-text-field>
                    <v-btn round color="purple" outline :disabled="edit" @click="addNewLabel">添加标签</v-btn>
                  </v-layout>
                  <v-divider class="my-3"></v-divider>
                  <v-layout row>
                    <v-flex xs7  pt-3 pb-3>
                      <el-upload class="upload-demo" ref="upload" action="http://localhost:80"
                                 :on-preview="handlePreview" :on-change="handleChange" :on-remove="handleRemove" :on-error="handleError" :http-request='submitUpload'
                                 :file-list="picList" list-type="picture" multiple  :auto-upload="false" :disabled="edit">
                        <el-button slot="trigger" size="small" type="primary" :disabled="edit">选取文件</el-button>
                        <div class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                      </el-upload>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs4><v-slider v-model="taskInfo.creditPerPic" color="purple" label="每张图片奖励" hint="Be careful" min="1" max="10" thumb-label :disabled="edit"></v-slider></v-flex>
                    <v-flex xs2><v-text-field v-model="taskInfo.creditPerPic" type="number" prepend-icon="monetization_on" color="purple" :disabled="edit"></v-text-field></v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs6>
                      <v-slider v-model="taskInfo.creditFinished" color="purple" label="完成任务奖励" hint="Be careful" min="1" max="200" thumb-label :disabled="edit"></v-slider>
                    </v-flex>
                    <v-flex xs2><v-text-field v-model="taskInfo.creditFinished" type="number" prepend-icon="monetization_on" color="purple" :disabled="edit"></v-text-field></v-flex>
                  </v-layout>
                  <v-divider class="my-3"></v-divider>
                  <v-layout row>
                    <v-flex  xs6 pl-4 pt-2>
                      <v-menu ref="s_menu" :close-on-content-click="false" v-model="s_menu" :nudge-right="40"
                              :return-value.sync="taskInfo.startTime" lazy transition="scale-transition" offset-y
                              full-width min-width="290px">
                        <v-text-field slot="activator" v-model="taskInfo.startTime" label="任务开始日期" prepend-icon="event"
                                      readonly color="purple" :disabled="edit"></v-text-field>
                        <v-date-picker v-model="taskInfo.startTime" no-title scrollable>
                          <v-spacer></v-spacer>
                          <v-btn flat color="primary" @click="s_menu = false">Cancel</v-btn>
                          <v-btn flat color="primary" @click="$refs.s_menu.save(taskInfo.startTime)">OK</v-btn>
                        </v-date-picker>
                      </v-menu>
                    </v-flex>
                    <v-flex  xs6 pl-4 pt-2>
                      <v-menu ref="e_menu" :close-on-content-click="false" v-model="e_menu" :nudge-right="40"
                              :return-value.sync="taskInfo.endTime" lazy transition="scale-transition" offset-y
                              full-width min-width="290px">
                        <v-text-field slot="activator" v-model="taskInfo.endTime" label="任务结束日期" prepend-icon="event"
                                      readonly color="purple" :disabled="edit"></v-text-field>
                        <v-date-picker v-model="taskInfo.endTime" no-title scrollable :min="taskInfo.startTime">
                          <v-spacer></v-spacer>
                          <v-btn flat color="primary" @click="e_menu = false">Cancel</v-btn>
                          <v-btn flat color="primary" @click="$refs.e_menu.save(taskInfo.endTime)">OK</v-btn>
                        </v-date-picker>
                      </v-menu>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card-text>
              <v-speed-dial v-model="fab" fixed bottom right direction="top" open-on-hover transition="slide-y-reverse-transition">
                <v-btn slot="activator" v-model="fab" color="orange" dark fab>
                  <v-icon class="pt-3">build</v-icon>
                  <v-icon class="pt-3">close</v-icon>
                </v-btn>
                <v-btn  fab dark small color="green" @click="edit=!edit">
                  <v-layout class="justify-center align-center"><v-icon >edit</v-icon></v-layout>
                </v-btn>
                <v-btn fab dark small color="indigo" @click="clearContent">
                  <v-layout class="justify-center align-center"><v-icon>clear</v-icon></v-layout>
                </v-btn>
                <v-btn fab dark small color="red" @click="recoverContent">
                  <v-layout class="justify-center align-center"><v-icon>undo</v-icon></v-layout>
                </v-btn>
              </v-speed-dial>
              <v-btn color="purple darken-1" dark class="mb-5" @click="updatePublish">更新发布<v-icon right>pan_tool</v-icon></v-btn>
              <v-btn color="purple lighten-1" dark class="mb-5" @click="draftTask">存为草稿<v-icon right >gesture</v-icon></v-btn>
              <v-btn color="grey" dark class="mb-5" @click="deleteTask">删除任务<v-icon right >delete</v-icon></v-btn>
            </v-card>
          </v-flex>
        </v-layout>
      </v-card>
    </v-content>
    <v-footer class="purple darken-2" id="footer" app>
      <v-layout row wrap align-center>
        <v-flex xs12 text-xs-center>
          <div class="white--text ml-3">
            Made with
            <v-icon class="red--text">favorite</v-icon>
            by AlphaCat Team
          </div>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
  import bus from '@/eventBus'
  export default {
    name: "IdleTask",
    data() {
      return{
        barTitle: '任务详情——未开始任务',
        taskType: '',
        taskState: 'idle',
        edit: true,
        picChanged: false,
        originalTaskInfo: '',
        taskInfo: {
          description: '',
          name: '',
        },
        picList: [],
        newLabelName: '',
        s_menu: false,
        e_menu: false,
        fab: false,
        picUploadError: false
      }
    },
    methods: {
      initiate(){
        this.$http.get('/task/'+this.$route.params.taskId).then((response)=>{
            this.taskInfo=response.data;
            this.originalTaskInfo=JSON.stringify(response.data);
            if(this.taskInfo.method==1)this.taskType='单框标注';
            else if(this.taskInfo.method==2)this.taskType='多框标注';
            else this.taskType='不规则标注';
          }
        );
        this.$http.get('/pic/'+this.$route.params.taskId).then((response)=>{
          this.picList=response.data;
        })
      },
      remove (item,index) {
        this.taskInfo.labels[index].choices.splice(this.taskInfo.labels[index].choices.indexOf(item), 1);
        this.taskInfo.labels[index].choices = [...this.taskInfo.labels[index].choices]
      },
      removeLabel(index){
        this.taskInfo.labels.splice(index,1);
        this.taskInfo.labels=[...this.taskInfo.labels]
      },
      addNewLabel(){
        if (this.$refs.newLabelField.value!=='') {
          let obj={};
          obj.label=this.$refs.newLabelField.value;
          obj.choices=[];
          this.taskInfo.labels.push(obj);
          this.taskInfo.labels=[...this.taskInfo.labels];
          this.$refs.newLabelField.reset();
        }
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
        this.picList = fileList;
      },
      handlePreview(file) {
        console.log(file);
      },
      handleChange(file, fileList) {
        console.log(fileList);
        this.picList = fileList;
        this.picChanged=true;
      },
      handleError(err, file, fileList){
        this.picUploadError=true;
        console.log(err);
        this.warning='图片未上传成功，请重新尝试！';
        this.snackbar=true;
      },
      submitUpload(content){
        //自定义的上传图片的方法
        //1. 创建formData 利用AXIOS传递
        let formData = new FormData;
        formData.append('file', content.file);
        let config = {
          'Content-Type': 'multipart/form-data'
        }
        let var_this = this;
        this.$http.put('/pic/'+this.taskInfo.taskId, formData,config)
          .then(function(response) {
            if (!response.data.success) {
              var_this.$message({
                message: response.data.message,
                type: 'error'
              });
            }
          })
          .catch(function(error) {
            console.log(error);
          })
      },
      clearContent() {
        this.taskInfo= {
          description: '',
          name: '',
        }
      },
      recoverContent(){
        this.taskInfo=JSON.parse(this.originalTaskInfo);
      },
      updatePublish(){
        console.log(this.originalTaskInfo);
        console.log(JSON.stringify(this.taskInfo));
        console.log(this.originalTaskInfo == JSON.stringify(this.taskInfo));
        if(JSON.stringify(this.taskInfo)==this.originalTaskInfo && this.picChanged==false && this.taskInfo.state == 'published'){
          this.edit=true;
          bus.$emit('show-info', '任务未进行任何更改！');
        }
        else{
          if(this.taskInfo.name === ''){
            bus.$emit('show-info', '无法发布！请填写任务名！');
          }
          else if(this.picList.length === 0){
            bus.$emit('show-info', '无法发布！请上传图片！');
          }
          else if(this.taskInfo.startTime === ''||this.taskInfo.endTime === ''){
            bus.$emit('show-info', '无法发布！请选择任务起止日期！');
          }
          else {
            this.taskInfo.state = 'published';
            let fail=false;
            if(this.taskInfo!=this.originalTaskInfo){
              this.$http.put('/task',this.taskInfo)
                .then((response)=>{})
                .catch((error)=>{
                  fail=true;
                })
            }
            if(this.picChanged==true){
              this.$refs.upload.submit();
            }
            if(fail==false && this.picUploadError==false) {
              this.edit = true;
              bus.$emit('show-info', '任务已更新发布！');
            }
          }
        }
      },
      deleteTask(){
        this.$http.post('/task/'+this.taskId+'/garbage').then((response)=>{
          this.edit=true;
          bus.$emit('show-info', '任务已丢入垃圾箱！');
        }).catch((error)=>{
          console.log(error);
          bus.$emit('show-info', '任务删除失败！');
        })
      },
      draftTask(){
        if(this.taskInfo==this.originalTaskInfo && this.picChanged==false && this.taskInfo.state == 'draft'){
          this.edit=true;
          bus.$emit('show-info', '任务未进行任何更改！');
        }
        else {
          this.taskInfo.state = 'draft';
          let fail=false;
          if(this.taskInfo!=this.originalTaskInfo){
            this.$http.put('/task',this.taskInfo)
              .then((response)=>{})
              .catch((error)=>{
                fail=true;
              })
          }
          if(this.picChanged==true){
            this.$refs.upload.submit();
          }
          if(fail==false && this.picUploadError==false) {
            this.edit = true;
            bus.$emit('show-info', '任务已更新发布！');
          }
        }
      }
    },
    created() {
      this.initiate();
    }
}
</script>

<style scoped>
  #footer{
    opacity: 0.8;
  }
</style>



