<template>
  <v-layout row>
    <v-flex>
      <v-card flat class="grey lighten-4">
        <v-container grid-list-lg fluid class="pt-5">
          <v-layout row wrap mx-2>
            <v-flex v-for="task in rubbish" :key="task.id" md2>
              <v-card hover :to="task.path">
                <v-card-media :src="`https://unsplash.it/150/300?image=${Math.floor(Math.random() * 75) + 1}`" height="130px">
                  <v-container fill-height class="picture">
                    <v-layout align-center>
                      <v-flex >
                        <span class="headline white--text">{{ task.name }}</span>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-media>
                <v-card-text class="grey--text pt-1 pb-1">
                  <v-layout align-center>
                    <v-btn icon @click="recoverAsDraft(task.id)"><v-icon color="purple darken-2">reply</v-icon></v-btn>
                    <v-spacer></v-spacer>
                    <v-btn icon @click="deleteForever(task.id)"><v-icon color="purple darken-2">delete</v-icon></v-btn>
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
       </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
    export default {
        name: "Garbage",
      data() {
        return {
          rubbish:[]
        }
      },
      methods: {
        initiate(){
          this.$http.get('/rubbish/'+sessionStorage.getItem('id'))
            .then((response)=>{
              this.rubbish=response.data;
            })
        },

        deleteForever(id){
          this.$http.delete('/task/'+id)
            .then((response)=>{
              this.initiate();
            })
        },

        recoverAsDraft(id){
          this.$http.post('/task/'+id+'/draft')
            .then((response)=>{
              this.initiate();
            })
        }
      },
      created(){
          this.initiate();
      }

    }
</script>

<style scoped>

</style>



