<template>
  <v-container fluid >
    <v-layout row class="pl-3 pt-2">
      <v-flex xs5>
        <croppa v-model="croppa" placeholder="点击此处上传头像" :quality="1" :zoom-speed="10" prevent-white-space initial-image="/api/avatar"></croppa>
      </v-flex>
      <!--
        <v-card v-bind:width=320>
          <v-layout class="align-center justify-center">
            <croppa v-model="croppa" v-bind:width=260  v-bind:height=250 placeholder="点击此处上传头像" :quality="1" :zoom-speed="10" :disabled="editInfo" prevent-white-space></croppa>
          </v-layout>
        </v-card>
        -->
    </v-layout>
    <v-layout row class="pl-3 pt-4">
      <v-flex xs5>
        <v-text-field :rules="[(v) => v.length <= 15 || 'Max 15 characters',(v)=>v.length>0 || 'User name is needed']" :counter="15" color="purple" v-model="requester.name" label="用户名" prepend-icon="account_box" required :disabled="editInfo"></v-text-field>
      </v-flex>
      <v-flex xs1></v-flex>
      <v-flex xs5 ><v-btn outline color="purple" :disabled="editInfo" @click="changePwd=true"><v-icon left>lock</v-icon>修改密码</v-btn></v-flex>
    </v-layout>
   <v-layout row justify-space-between class="pl-3 pt-3">
      <v-flex xs5>
        <v-menu
          ref="menu"
          :close-on-content-click="false"
          v-model="dateMenu"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          min-width=290
          :disabled="editInfo"
        >
          <v-text-field
            slot="activator"
            v-model="requester.birth"
            label="生日"
            prepend-icon="event"
            readonly
            required
            color="purple"
            :disabled="editInfo"
          ></v-text-field>
          <v-date-picker
            ref="picker"
            v-model="requester.birth"
            :max="new Date().toISOString().substr(0, 10)"
            min="1950-01-01"
            @change="save(requester.birth)"
            color="purple"
          ></v-date-picker>
        </v-menu>
      </v-flex>
    </v-layout>
    <v-layout row class="pl-3">
      <v-flex xs5>
        <v-radio-group v-model="requester.sex" row label="性别" :disabled="editInfo">
          <v-radio color="purple" label="男性" v-bind:value=0 :disabled="editInfo"></v-radio>
          <v-radio color="purple" label="女性" v-bind:value=1 :disabled="editInfo"></v-radio>
        </v-radio-group>
      </v-flex>
    </v-layout>
    <v-layout row class="pl-3">
      <v-flex xs5>
        <v-text-field v-model="requester.email" color="purple" label="邮箱" prepend-icon="email" type="email" required :disabled="editInfo"></v-text-field>
      </v-flex>
    </v-layout>
    <v-layout row class="pl-3">
      <v-flex xs5>
        <v-text-field v-model="requester.company" color="purple" prepend-icon="card_travel" label="公司" :disabled="editInfo"></v-text-field>
      </v-flex>
    </v-layout>
    <v-layout row class="pl-3">
      <v-flex xs5>
        <v-text-field v-model="requester.occupation" color="purple" prepend-icon="turned_in" label="职务" :disabled="editInfo"></v-text-field>
      </v-flex>
    </v-layout>
    <v-speed-dial v-model="fab" fixed bottom right direction="top" open-on-hover transition="slide-y-reverse-transition">
      <v-btn slot="activator" v-model="fab" color="orange" dark fab>
        <v-icon class="pt-3">build</v-icon>
        <v-icon class="pt-3">close</v-icon>
      </v-btn>
      <v-btn fab dark small color="green" @click="editInfo=!editInfo">
        <v-layout class="align-center justify-center"><v-icon>edit</v-icon></v-layout>
      </v-btn>
      <v-btn fab dark small color="indigo" @click="clear">
        <v-layout class="align-center justify-center"><v-icon>clear</v-icon></v-layout>
      </v-btn>
      <v-btn fab dark small color="red" @click="revert">
        <v-layout class="align-center justify-center"><v-icon>undo</v-icon></v-layout>
      </v-btn>
    </v-speed-dial>
    <v-btn color="purple darken-1" dark class="mb-5 ml-3" :disabled="editInfo" @click="submitChange">提交修改</v-btn>
    <v-dialog v-model="changePwd" max-width=500>
      <v-card>
        <v-card-title class="title">
          密码重设
        </v-card-title>
        <v-alert :value="alert" type="error" transition="scale-transition">
          {{alertInfo}}
        </v-alert>
        <v-card-text class="pt-0">
          <v-text-field v-model="oldPwd" color="purple"  label="旧密码" required></v-text-field>
          <v-text-field v-model="newPwd" color="purple"  label="新密码" required></v-text-field>
          <v-text-field v-model="newPwdCheck" color="purple"  label="新密码确认" required></v-text-field>
        </v-card-text>
        <v-card-actions class="pt-0">
          <v-spacer></v-spacer>
          <v-btn color="purple" flat @click.stop="checkPwd">确认</v-btn>
          <v-btn color="purple" flat @click.stop="changePwd=false">放弃</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-bind:timeout=6000 top v-model="snackbar">
      {{ warning }}
      <v-btn flat color="pink" @click.native="snackbar = false">Close</v-btn>
    </v-snackbar>
  </v-container>
</template>

<script>
    export default {
        name: "PersonalInfo",
      data() {
          return {
            editInfo: true,
            changePwd: false,
            dateMenu: false,
            fab: false,
            alert: false,
            alertInfo:'',
            croppa: {},
            oldPwd: '',
            newPwd: '',
            newPwdCheck: '',
            originalInfo: '',
            requester: {
              name: '',
            },
            hasSameName: {},
            checkData:{
              name:'',
              key:'',
              type: 'requester',
            },
            snackbar: false,
            warning: '',
            submitSucceed:  true,
          }
      },
      methods: {
        initiate(){
          this.$http.get('/requester/' + sessionStorage.getItem('id'))
            .then((response) => {
              this.requester = response.data;
              this.originalInfo=JSON.stringify(response.data);
              this.checkData.name=this.requester.name;
            })
        },
        save(date) {
          this.$refs.menu.save(date)
        },
        checkPwd(){
          this.checkData.key=this.oldPwd;
          this.$http.post('/login', this.checkData)
            .then((response) => {
              let result = response.data.result;
              if (result === 'success') {
                 if(this.newPwd!=this.newPwdCheck){
                   this.alertInfo='两次输入的新密码不相同！';
                   this.alert=true;
                 }else{
                   this.alert=false;
                   this.requester.key=this.newPwd;
                   this.changePwd=false;
                 }
              } else if (result === 'failed') {
                this.alertInfo='原密码不正确！';
                this.alert=true;
                this.$message.warning(response.data.info);
              }
            })
            .catch(function (error) {
              console.log(error);
            });

        },

        upload(name, type) {
          this.croppa.generateBlob((blob) => {
            let fd = new FormData();
            fd.append('file', blob);
            fd.append('name', name);
            fd.append('type', type);
            this.$http.post('/avatar', fd)
              .then((response) => {

              })
          })
        },
        clear(){
          this.requester={name: ''};
        },

        revert(){
          this.requester=JSON.parse(this.originalInfo);
        },

        submitChange(){
          if(JSON.stringify(this.requester)==this.originalInfo && this.croppa.hasImage()==false){
            this.editInfo=true;
            this.warning='未进行任何修改！';
            this.snackbar=true;
            this.submitSucceed=false;
          }else {
            if (JSON.stringify(this.requester) != this.originalInfo) {
              this.$http.put('/requester/' + sessionStorage.getItem('id'), this.requester)
                .then((response) => {})
                .catch((error) => {
                  this.submitSucceed=false;
                  console.log(error);
                })
            }
            if(this.croppa.hasImage()==true){
              this.croppa.generateBlob((blob) => {
                let fd = new FormData();
                fd.append('file', blob);
                fd.append('name', this.requester.name);
                fd.append('type', 'requester');
                this.$http.post('/avatar', fd)
                  .then((response) => {
                  })
                  .catch((error)=>{
                    this.submitSucceed=false;
                    console.log(error);
                  })
              })
            }
          }
          if(this.submitSucceed==true){
            this.editInfo=true;
            this.warning='已更新您的个人信息！'
            this.snackbar=true;
          }
        },
      },
      created() {
          this.initiate();
      }
    }
</script>

<style scoped>

</style>



