<template>

  <v-card>
    <v-card-title>
      <span class="headline">任务数据统计</span>
    </v-card-title>
    <v-card-text>
      <v-container>
        <v-layout row class="headline">
          <v-flex xs6>
            <span>已发布任务总数：</span>
            <ICountUp :endVal="taskNum" @ready="onReady"></ICountUp>
          </v-flex>
          <v-flex xs6>
            <span>已结束任务总数：</span>
            <ICountUp :endVal="finishTaskNum" @ready="onReady"></ICountUp>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex xs12>
            <LineChart :data="lineChartData.data" :options="lineChartData.options" :height="lineChartData.height"></LineChart>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card-text>
  </v-card>
</template>

<script>
  import LineChart from '@/components/charts/LineChart';
  import ICountUp from 'vue-countup-v2';
  export default {
    name: "TaskStatistics",
    components:{
      LineChart, ICountUp
    },
    data() {
      return {
        taskNum: 0,
        finishTaskNum: 0,
        lineChartData: {
          data: {
            datasets: [
              {
                label: '已完成任务数量',
                data: [],
                backgroundColor: ['rgba(250, 51, 0, 0.5)']
              },
              {
                label: '任务总数',
                data: [],
                backgroundColor: ['rgba(255, 100, 61, 0.5)']
              }],
            labels: this.getDateList()
          },
          options: {
            responsive: true,
            title: {
              display: true,
              text: '任务组成变化',
              position: 'bottom',
            }
          },
          height: 200
        },
      }
    },
    methods: {
      getDateList() {
        let dateList = [];
        let date = new Date();
        for (let i = 0; i < 7; i++) {
          date.setDate(date.getDate()-1);
          dateList.unshift((date.getMonth() + 1) + "-" + date.getDate());
        }
        return dateList;
      },
      onReady(instance, CountUp) {
        const that = this;
        instance.update(that.endVal + 100);
      }
    },
    mounted() {
      this.$http.get('/statistics/task')
        .then((response) => {
          let data = response.data;
          this.taskNum = data.taskNum;
          this.finishTaskNum = data.finishNum;
          this.lineChartData.data.datasets[1].data = data.taskNumArray;
          this.lineChartData.data.datasets[0].data = data.finishNumArray;
          let ctx = document.getElementById("line-chart");
          let myChart = new Chart(ctx, {type: 'line', data: this.lineChartData.data, options: this.lineChartData.options});
        })
    }
  }
</script>

<style scoped>

</style>



