<template>
  <v-layout row>
    <v-flex>
      <v-card flat class="grey lighten-4">
        <v-container grid-list-lg fluid class="pt-5">
          <v-layout row wrap mx-2>
            <v-flex v-for="task in drafts" :key="task.id" md2>
              <v-card hover :to="task.path">
                <v-card-media :src="`https://unsplash.it/150/300?image=${Math.floor(Math.random() * 100) + 1}`" height="130px">
                  <v-container fill-height class="picture">
                    <v-layout align-center>
                      <v-flex >
                        <span class="headline white--text">{{ task.name }}</span>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-media>
                <v-card-text class="grey--text pt-1 pb-1">
                  <v-layout align-center>
                    <v-spacer></v-spacer>
                    <v-btn icon @click="garbage(task.id)"><v-icon color="purple darken-2">delete</v-icon></v-btn>
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
    export default {
        name: "Draft",
      data() {
          return {
            drafts: []
          }
      },
      methods: {
          initiate(){
            this.$http.get('/drafts/'+sessionStorage.getItem('id'))
              .then((response)=>{
                this.drafts=response.data;
                for(var i=0;i<this.drafts.length;i++){
                  this.drafts[i].path='/requester/idleTask/'+this.drafts[i].id;
                }
              })
          },
        garbage(id){
            this.$http.post('/task/'+id+'/garbage')
              .then((response)=>{
                this.initiate();
              })
        }
      },
      created(){
          this.initiate();
      }
    }
</script>

<style scoped>

</style>



