<template>
  <v-app>
    <v-toolbar color="purple darken-2" dark fixed app clipped-right id="toolbar">
      <v-toolbar-title>{{barTitle}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-tooltip bottom>
        <v-btn slot="activator" icon large target="_blank" to="/requester/"><v-icon large>home</v-icon></v-btn>
        <span>返回主页</span>
      </v-tooltip>
    </v-toolbar>
    <v-content>
    <v-container fluid>
      <v-layout row wrap align-center justify-center>
        <v-flex xs10>
          <v-expansion-panel expand>
            <v-expansion-panel-content>
              <div slot="header" class="title purple--text">任务信息</div>
              <v-card>
                <v-layout row class="pl-5 pt-3">
                  <v-flex xs4 ><div class="body-2">任务名称：&emsp;&emsp;{{taskInfo.name}}</div></v-flex>
                  <v-flex xs4><div class="body-2">任务类型：&emsp;&emsp;{{taskType}}</div></v-flex>
                </v-layout>
                <v-layout row class="pl-5 pt-3">
                  <v-flex v-if="taskInfo.method===1" xs4><div class="body-2">任务需求：&emsp;&emsp;{{taskInfo.hasWholeLabel ? "需要整体描述" : "无需整体描述"}}</div></v-flex>
                  <v-flex xs4><div class="body-2">任务时间：&emsp;&emsp;{{taskInfo.startTime}}--{{taskInfo.endTime}}</div></v-flex>
                </v-layout>
                <v-layout row class="pl-5">
                  <v-flex xs8><v-text-field name="taskDescription" label="任务描述" textarea v-model="taskInfo.description" :disabled="true" color="purple" rows="3"></v-text-field></v-flex>
                </v-layout>
                <v-layout v-if="taskInfo.method===1" row class="pl-5">
                  <v-flex xs2><div class="body-2">任务标签及预设选项:</div></v-flex>
                </v-layout>
                <v-layout v-if="taskInfo.method===1" row class="pl-5 pt-2">
                  <v-flex xs4>
                    <v-list style="background: transparent" class="elevation-0">
                      <v-list-group v-for="item in taskInfo.labels" :key="item.label" no-action>
                        <v-list-tile slot="activator" color="purple">
                          <v-list-tile-action>
                            <v-icon color="purple">label</v-icon>
                          </v-list-tile-action>
                          <v-list-tile-content>
                            <v-list-tile-title>{{ item.label }}</v-list-tile-title>
                          </v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile v-for="subItem in item.choices" :key="subItem">
                          <v-list-tile-content>
                            <v-list-tile-title>{{ subItem}}</v-list-tile-title>
                          </v-list-tile-content>
                        </v-list-tile>
                      </v-list-group>
                    </v-list>
                  </v-flex>
                </v-layout>
                <v-layout row class="pl-5 pt-2"><v-flex xs2><div class="body-2">任务图片:</div></v-flex></v-layout>
                <v-layout row class="pl-5 pt-2 pb-5">
                  <v-flex xs8>
                    <v-card style="max-height: 200px" class="scroll-y">
                      <v-container grid-list-sm fluid>
                        <v-layout row wrap>
                          <v-flex v-for="pic in picList" :key="pic.name" xs2>
                            <v-card flat tile>
                              <v-card-media :src="`https://unsplash.it/150/300?image=${Math.floor(Math.random() * 100) + 1}`" height="100px"></v-card-media>
                            </v-card>
                          </v-flex>
                        </v-layout>
                      </v-container>
                    </v-card>
                  </v-flex>
                </v-layout>
              </v-card>
            </v-expansion-panel-content>
            <v-expansion-panel-content>
              <div slot="header" class="title purple--text">标注情况</div>
              <v-card>
                <v-layout row class="pl-5 pt-3">
                  <v-flex xs4 ><div class="body-2">参与任务人数：&emsp;&emsp;{{taskInfo.workerCount}}</div></v-flex>
                  <v-flex xs4><div class="body-2">图片标注率：&emsp;&emsp;{{taskInfo.tagRate}}</div></v-flex>
                </v-layout>
                <v-layout row class="pl-5 pt-3 pb-5">
                  <v-flex xs4><div class="body-2">消耗总积分：&emsp;&emsp;{{taskInfo.costCredit}}</div></v-flex>
                </v-layout>
              </v-card>
            </v-expansion-panel-content>
            <v-expansion-panel-content value="3">
              <div slot="header" class="title purple--text">任务结果</div>
              <v-card height="200px">

              </v-card>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-flex>
      </v-layout>
    </v-container>
    </v-content>
    <v-footer class="purple darken-2" id="footer" app>
      <v-layout row wrap align-center>
        <v-flex xs12 text-xs-center>
          <div class="white--text ml-3">
            Made with
            <v-icon class="red--text">favorite</v-icon>
            by AlphaCat Team
          </div>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
    export default {
        name: "FinishedTask",
      data() {
          return {
            barTitle: '任务详情——进行中任务',
            taskType: '单框标注',
            taskInfo: {},
            picList: [
              {name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'},
              {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'},
            ],
          }
      },
      methods:{
          initiate(){
            this.$http.get('/task/' + this.$route.params.taskId + '/requester')
              .then((response) => {
                this.taskInfo=response.data;
                if (this.taskInfo.method == 1) this.taskType = '单框标注';
                else if (this.taskInfo.method == 2) this.taskType = '多框标注';
                else this.taskType = '不规则标注';
              });
          }
      },
      created(){
          this.initiate();
      }
    }
</script>

<style scoped>
  #footer{
    opacity: 0.8;
  }
</style>



