<template>
  <v-layout row>
    <v-flex>
      <v-card flat class="grey lighten-4">
        <br/>
        <v-toolbar color="transparent" flat>
          <v-spacer></v-spacer>
          <v-toolbar-items>
          <v-select :items="taskOverviewTypes" v-model="taskOverviewType" label="任务筛选" style="width: 200px" color="purple darken-2"></v-select>
          </v-toolbar-items>
        </v-toolbar>
        <v-container grid-list-lg fluid>
          <v-layout row wrap mx-2>
            <v-flex v-for="task in overviewTasks" :key="task.name" md2>
              <v-card hover :to="task.path">
                <v-card-media :src="`https://unsplash.it/150/300?image=${Math.floor(Math.random() * 75) + 1}`" height="130px">
                  <v-container fill-height class="picture">
                    <v-layout align-center>
                      <v-flex >
                        <span class="headline white--text">{{ task.name }}</span>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-media>
                <v-card-text class="grey--text">
                  <v-layout align-center>
                    <v-icon v-if="task.state===0" color="grey">remove</v-icon>
                    <v-icon v-else-if="task.state===100" color="purple">done_all</v-icon>
                    <v-progress-circular v-else :value="task.state" color="purple" size="24"></v-progress-circular>
                    <v-spacer></v-spacer>
                    <v-icon color="purple">person</v-icon>
                    {{task.workerCount}}
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
        <v-btn fixed dark fab large bottom right color="orange darken-1" @click.stop="initCreateDialog">
          <v-layout align-center justify-center>
            <v-icon large>add</v-icon>
          </v-layout>
        </v-btn>
      </v-card>
    </v-flex>
    <v-dialog v-model="createDialog" fullscreen hide-overlay transition="dialog-bottom-transition" scrollable>
      <v-card tile>
        <v-toolbar card dark color="purple">
          <v-toolbar-title>任务创建</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn dark flat @click.native="publishNewTask">发布任务</v-btn>
            <v-btn dark flat @click.native="saveAsDraft">存为草稿</v-btn>
            <v-btn dark flat @click.native="createDialog=false">放弃创建</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-card-text>
          <v-stepper v-model="stepOrder" vertical class="elevation-0">
            <v-stepper-step :complete="stepOrder > 1" step="1">
              选择任务类型
            </v-stepper-step>
            <v-stepper-content step="1">
              <v-card color="purple lighten-5" class="mb-4" height="80px" pr-5>
                <v-flex sm6 pl-3 pt-2>
                  <v-radio-group v-model="newTask.method" row>
                    <v-radio label="单框标注" value="1" color="purple"></v-radio>
                    <v-radio label="多框标注" value="2" color="purple"></v-radio>
                    <v-radio label="不规则标注" value="3"  color="purple"></v-radio>
                  </v-radio-group>
                </v-flex>
              </v-card>
              <v-btn color="purple" dark @click.native="stepOrder = 2">下一步</v-btn>
            </v-stepper-content>
            <v-stepper-step :complete="stepOrder > 2" step="2" editable>
              配置任务项
            </v-stepper-step>
            <v-stepper-content step="2">
              <v-card color="purple lighten-5" class="mb-4">
                <v-flex sm6 pl-3 pt-3 pb-3>
                  <v-text-field :rules="[(v) => v.length <= 15 || 'Max 15 characters',(v)=>v.length>0 || 'Task name is needed']" :counter="15" v-model="newTask.name" label="任务名称" color="purple" ref="taskNameField"></v-text-field>
                  <v-text-field :rules="[(v) => v.length <= 150 || 'Max 150 characters']" :counter="150" label="任务描述" v-model="newTask.description" textarea rows="3" color="purple" ref="taskDesField"></v-text-field>
                  <v-divider class="my-3"></v-divider>
                  <v-checkbox v-if="newTask.method==='1'" label="是否整体描述" v-model="newTask.hasWholeLabel" color="purple"></v-checkbox>
                  <v-subheader v-if="newTask.method==='1'">任务标签及预设选项</v-subheader>
                  <v-container v-if="newTask.method==='1'" fluid grid-list-sm>
                    <v-layout v-for="(tag,index) in newTask.labels" :key="tag.label" row wrap>
                      <v-select v-model="tag.choices" :label="tag.label" chips tags solo prepend-icon="bookmark_border"
                                clearable color="purple" style="background: transparent" pd-1 ref="taskLabelField">
                        <template slot="selection" slot-scope="data">
                          <v-chip :selected="data.selected" close @input="remove(data.item,index)">
                            <strong>{{ data.item }}</strong>&nbsp;
                            <span>({{tag.label}})</span>
                          </v-chip>
                        </template>
                      </v-select>
                      <v-btn outline small fab color="purple" @click="removeLabel(index)">
                        <v-layout align-center justify-center><v-icon>delete_forever</v-icon></v-layout>
                      </v-btn>
                    </v-layout>
                  </v-container>
                  <v-container v-if="newTask.method==='1'" fluid>
                    <v-layout row>
                      <v-flex xs10>
                        <v-text-field :rules="[(v) => v.length <= 10 || 'Max 10 characters']" :counter="10"
                                                 id="newLabelField" name="newLabelField" label="新标签名" color="purple"
                                                 v-model="newLabelName" ref="newLabelField" prepend-icon="bookmark"></v-text-field></v-flex>
                      <v-flex xs2><v-btn round color="purple" outline @click="addNewLabel">添加标签</v-btn></v-flex>
                  </v-layout>
                  </v-container>
                  <v-divider v-if="newTask.method==='1'" class="my-3"></v-divider>
                  <v-container fluid class="pb-0">
                    <v-layout row>
                      <v-flex xs6><v-slider v-model="newTask.creditPerPic" color="purple" label="每张图片奖励" hint="Be careful" min="1" max="10" thumb-label></v-slider></v-flex>
                      <v-flex xs1></v-flex>
                      <v-flex xs2><v-text-field v-model="newTask.creditPerPic" type="number" min="1" prepend-icon="monetization_on" color="purple"></v-text-field></v-flex>
                    </v-layout>
                  </v-container>
                  <v-container fluid class="pt-0">
                    <v-layout row>
                      <v-flex xs9><v-slider v-model="newTask.creditFinished" color="purple" label="完成任务奖励" hint="Be careful" min="1" max="200" thumb-label></v-slider></v-flex>
                      <v-flex xs1></v-flex>
                      <v-flex xs2> <v-text-field v-model="newTask.creditFinished" min="1" type="number"  prepend-icon="monetization_on" color="purple"></v-text-field></v-flex>
                    </v-layout>
                  </v-container>
                </v-flex>
              </v-card>
              <v-btn color="purple" dark @click.native="stepOrder = 3">下一步</v-btn>
              <v-btn flat color="purple" dark @click.native="stepOrder = 1">上一步</v-btn>
            </v-stepper-content>
            <v-stepper-step :complete="stepOrder > 3" step="3" color="purple">上传图片</v-stepper-step>
            <v-stepper-content step="3">
              <v-card color="purple lighten-5" class="mb-4 scroll-y" style="max-height: 400px">
                <v-flex sm4 pl-3 pt-3 pb-3>
                  <el-upload class="upload-demo" ref="upload" action="http://localhost:80"
                             :on-preview="handlePreview" :on-change="handleChange" :on-remove="handleRemove" :http-request='submitUpload'
                             :file-list="picUploadList" list-type="picture" multiple  :auto-upload="false">
                    <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                    <div class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                  </el-upload>
                </v-flex>
              </v-card>
              <v-btn color="purple" dark @click.native="stepOrder = 4">下一步</v-btn>
              <v-btn flat color="purple" @click.native="stepOrder = 2">上一步</v-btn>
            </v-stepper-content>
            <v-stepper-step step="4" >选择任务时间</v-stepper-step>
            <v-stepper-content step="4">
              <v-card color="purple lighten-5" class="mb-4" height="180px">
                <v-flex  sm6 pl-4 pt-2>
                  <v-menu ref="s_menu" :close-on-content-click="false" v-model="s_menu" :nudge-right="40" :return-value.sync="newTask.startTime"
                          lazy transition="scale-transition" offset-y full-width min-width="290px">
                    <v-text-field slot="activator" v-model="newTask.startTime" label="任务开始日期" prepend-icon="event" readonly color="purple" ref="taskSTField"></v-text-field>
                    <v-date-picker v-model="newTask.startTime" no-title scrollable :min="new Date(new Date().getTime()+24*60*60*1000).toISOString().substr(0, 10)">
                      <v-spacer></v-spacer>
                      <v-btn flat color="primary" @click="s_menu = false">Cancel</v-btn>
                      <v-btn flat color="primary" @click="$refs.s_menu.save(newTask.startTime)">OK</v-btn>
                    </v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex  sm6 pl-4 pt-2>
                  <v-menu ref="e_menu" :close-on-content-click="false" v-model="e_menu" :nudge-right="40" :return-value.sync="newTask.endTime"
                          lazy transition="scale-transition" offset-y full-width min-width="290px">
                    <v-text-field slot="activator" v-model="newTask.endTime" label="任务结束日期" prepend-icon="event" readonly color="purple" ref="taskSTField"></v-text-field>
                    <v-date-picker v-model="newTask.endTime" no-title scrollable :min="newTask.startTime">
                      <v-spacer></v-spacer>
                      <v-btn flat color="primary" @click="e_menu = false">Cancel</v-btn>
                      <v-btn flat color="primary" @click="$refs.e_menu.save(newTask.endTime)">OK</v-btn>
                    </v-date-picker>
                  </v-menu>
                </v-flex>
              </v-card>
              <v-btn color="purple" dark @click.native="stepOrder = 3">上一步</v-btn>
            </v-stepper-content>
          </v-stepper>
        </v-card-text>
        <div style="flex: 1 1 auto;"></div>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import bus from '@/eventBus'
  export default {
      name: "TaskOverview",
    data() {
        return {
          createDialog: false,
          stepOrder: 1,
          taskOverviewTypes: [
            {text: '全部任务', value: 'all'},
            {text: '已结束', value: 'history'},
            {text: '进行中', value: 'underway'},
            {text: '未开始', value: 'notstart'},
          ],
          taskOverviewType: 'all',
          overviewTasks: [],
          newTask: {
            requesterId: '',
            name: '',
            description: '',
            creditPerPic: '1',
            creditFinished: '1',
            method: "1",
            hasWholeLabel: true,
            startTime: '',
            endTime: '',
            labels: [],
            state: ''
          },
          newTaskId: {},
          picUploadList:[],
          newLabelName: '',
          s_menu: false,
          e_menu: false,
          picIndex: 1,
        }
    },
    methods: {
      initCreateDialog(){
        this.stepOrder = 1;
        this.newTask.name='';
        this.newTask.description='';
        this.newTask.creditFinished='1';
        this.newTask.creditPerPic='1';
        this.newTask.method='1';
        this.newTask.hasWholeLabel=true;
        this.$refs.upload.clearFiles();
        this.newTask.startTime='';
        this.newTask.endTime='';
        this.newTask.labels=[];
        this.createDialog=true;

      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
        this.picUploadList = fileList;
      },
      handlePreview(file) {
        console.log(file);
      },
      handleChange(file, fileList) {
        console.log(fileList);
        this.picUploadList = fileList;
      },
      remove (item,index) {
        this.newTask.labels[index].choices.splice(this.newTask.labels[index].choices.indexOf(item), 1);
        this.newTask.labels[index].choices = [...this.newTask.labels[index].choices]
      },
      addNewLabel(){
        if (this.$refs.newLabelField.value!=='') {
          let obj={};
          obj.label=this.$refs.newLabelField.value;
          obj.choices=[];
          this.newTask.labels.push(obj);
          this.newTask.labels=[...this.newTask.labels];
          this.$refs.newLabelField.reset();
          this.newLabelName = '';
        }
      },
      removeLabel(index){
        this.newTask.labels.splice(index,1);
        this.newTask.labels=[...this.newTask.labels]
      },

      getTaskList(){
        this.$http.get('/task?requesterId='+sessionStorage.getItem('id')+'&type='+this.taskOverviewType)
          .then((response)=>{
            this.overviewTasks=response.data;
            for(let i=0;i<this.overviewTasks.length;i++){
              if(this.overviewTasks[i].state === 0)this.overviewTasks[i].path='/requester/idleTask/'+this.overviewTasks[i].id;
              else if(this.overviewTasks[i].state>0 && this.overviewTasks[i].state<100) this.overviewTasks[i].path='/requester/underwayTask/'+this.overviewTasks[i].id;
              else this.overviewTasks[i].path='/requester/finishedTask/'+this.overviewTasks[i].id;
            }
          });
      },
      submitUpload(content){
        //自定义的上传图片的方法
        //1. 创建formData 利用AXIOS传递
        console.log(content);
        let formData = new FormData;
        formData.append('file', content.file);
        formData.append('picIndex', this.picIndex);
        this.picIndex ++;
        let config = {
          'Content-Type': 'multipart/form-data'
        };
        this.$http.post('/pic/'+this.newTaskId.taskId, formData, config)
          .then(function(response) {
            if (!response.data.success) {
              bus.$emit('show-info', response.data.message);
            }
          })
          .catch(function(error) {
            console.log(error);
          })
      },
      publishNewTask(){/*
        if(this.newTask.name === ''){
          bus.$emit('show-info', '无法发布！请填写任务名！');
        }
        else if(this.picUploadList.length === 0){
          bus.$emit('show-info', '无法发布！请上传图片！');
        }
        else if(this.newTask.startTime === ''||this.newTask.endTime === ''){
          bus.$emit('show-info', '无法发布！请选择任务起止日期！');
        }
        else{*/
          this.newTask.state = 'published';
          this.$http.post('/task',this.newTask)
            .then((response)=>{
              this.newTaskId=response.data;
              //console.log(JSON.stringify(this.newTaskId));
              this.$refs.upload.submit();
            });
          bus.$emit('show-info', '成功发布任务！');
          this.createDialog=false;
        //}
      },

      saveAsDraft: function () {
        this.newTask.state='draft';
        this.$http.post('/task',this.newTask)
          .then((response)=>{
            this.newTaskId=response.data;
            console.log(JSON.stringify(this.newTaskId));
            if(this.picUploadList !== [])
              this.$refs.upload.submit();
          });
        bus.$emit('show-info', '任务已存为草稿，可至草稿箱查看。');
        this.createDialog=false;
      },

    },
    created(){
      this.newTask.requesterId=sessionStorage.getItem('id');
      this.getTaskList();
    }
  }
</script>

<style scoped>
.picture{
  background-color: dimgray;
  opacity: 0.7;
}
</style>



